 
  .voting{
    margin-top: 350px;
    background-color: white;
    height:100vh;
  }
  
  .content{
    margin-top: -60px;
    display: flex;
    justify-content: center;
    font-family: "Trirong", serif;
    
  }

  .contentProposal{
    color: rgb(3, 25, 66);
    font-weight: bold;
    font-size: 26px;
    width:60vw;
    left: 20vw;
    position: absolute;
  }

  .proposal{
    margin-bottom: 40px;

  }

  .votesDiv{
    display: flex;
    justify-content: space-between;
  }

  .backHome {
    display: flex;
    color: rgb(138, 8, 23);
    justify-content: start;
    margin-bottom: 20px;
    margin-top: -60px;
    font-size: 18px;
    align-items: center;
    gap: 2px;
  }

  .proposalOverview{
    display: flex;
    justify-content: space-between;
    margin-top: 10px;
  }

  .proposer {
    display: flex;
    font-size: 12px;
    gap: 10px;
    color:rgba(0, 0, 0, 0.5);
    align-items: center;
  }
  
  .tabContent {
    left: 20vw;
    position: absolute;
    width:60vw;
    height: 20vw;
    margin-top: 20px;
    color: rgb(12, 6, 87);
    font-weight: bold;
    font-size: 26px;
  }
  
  .widgets { 
    font: "Trirong", serif;
    display: flex;
    justify-content:space-between;
    gap:20px;
    height: 150px;
    margin-top: 20px;
    margin-bottom: 40px;
   
    
    
  }

  
  .extraWidgetInfo{
    color:rgba(6, 158, 252, 0.5);
    font-size: 12px;
    width: 50%;
    margin-top: 10px;
  }
  
  .extraTitle{
    display: flex;
    justify-content: end;
  }
  
  .progress{
    margin-top: 20px;
    height:8px;
    background-color:rgb(222, 222, 222);
    border-radius: 4px;
  }
  
  .progressPercentage{
    height:8px;
    background-color:rgba(6, 158, 252, 0.5);
    border-radius: 4px;
  }
  
